<template>
    <div class="all_com">
        <div class="title_com">订单详情（线下）</div>
        <!-- 订单详情 -->
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>订单详情（线下）</span>
            </div>
            <table class="order_detail">
                <tr>
                    <th>ID</th>
                    <th>用户名</th>
                    <th>商品ID</th>
                    <th>商品名</th>
                    <th>类型</th>
                    <th>单价</th>
                    <th>数量</th>
                    <th>优惠金额</th>
                    <th>总价</th>
                    <th>支付方式</th>
                    <th>状态</th>
                    <th>已支付总额</th>
                </tr>
                <tr>
                    <td>{{orderInfo.Id}}</td>
                    <td>{{orderInfo.OfflineUser.Account}}</td>
                    <td>{{orderInfo.Product.ID}}</td>
                    <td>{{orderInfo.Product.Name}}</td>
                    <td>{{orderInfo.Product.IsPresell?'预售':'全款'}}</td>
                    <td>{{orderInfo.ProductOriginPrice}}</td>
                    <td>{{orderInfo.Amount}}</td>
                    <td>{{orderInfo.Discount}}</td>
                    <td>{{orderInfo.TotalShouldPayPrice}}</td>
                    <td>
                        <div v-if="orderInfo.IsPresell">
                            <span v-if="orderInfo.PresellPayment == 'UnionPay'">人民币</span>
                            <span v-else-if="orderInfo.PresellPayment == 'BTC'">BTC</span>
                            <span v-else>{{orderInfo.PresellPayment}}</span>
                        </div>
                        <div v-else>
                            <span v-if="orderInfo.Payment == 'UnionPay'">人民币</span>
                            <span v-else-if="orderInfo.Payment == 'BTC'">BTC</span>
                            <span v-else>{{orderInfo.Payment}}</span>
                        </div>
                    </td>
                    <td>
                        <span v-if="this.status == 'UNPAID'">待确认支付</span>
                        <span v-else-if="this.status == 'PRESELL_PAID'">已支付定金</span>
                        <span v-else-if="this.status == 'WAIT_FOR_FINAL_PAY'">待支付尾款</span>
                        <span v-else-if="this.status == 'PAID'">已完成支付</span>
                        <span v-else-if="this.status == 'UNDELIVERED' && orderInfo.IsHostingOrder == false && orderInfo.ReadyDeliver == 0">待发货（发货）</span>
                        <span v-else-if="this.status == 'UNDELIVERED' && orderInfo.IsHostingOrder == true && orderInfo.ReadyDeliver == 0">待发货（托管）</span>
                        <span v-else-if="this.status == 'DELIVERED'  && orderInfo.IsHostingOrder == false">已发货（发货）</span>
                        <span v-else-if="this.status == 'DELIVERED'  && orderInfo.IsHostingOrder == true">已发货（托管）</span>
                        <span v-else-if="this.status == 'COMPLETED'">完成订单</span>
                        <span v-else-if="this.status == 'CANCELED'">已取消</span>
                        <span v-else>{{this.status}}</span>
                    </td>
                    <td>{{totalPrice}}</td>
                </tr>
            </table>
        </el-card>
        <!-- 订单备注信息（所有状态可编辑）-->
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>订单备注信息</span>
            </div>
            <table class="order_detail remark_detail">
                <tr>
                    <td>备注</td>
                    <td style="border:none">
                        <el-input
                                type="textarea"
                                :rows="2"
                                v-model="remarks">
                        </el-input>
                    </td>
                </tr>
            </table>
            <el-button type="primary" style="margin:20px 0 0 29px" @click="remarksSave">保存</el-button>
        </el-card>
        <!-- 用户信息（不可编辑） -->
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>用户信息</span>
            </div>
            <table class="order_detail reserve_detail">
                <tr>
                    <td>用户名</td>
                    <td>{{orderInfo.OfflineUser.Account}}</td>
                </tr>
                <tr>
                    <td>手机</td>
                    <td>{{orderInfo.OfflineUser.PhonePrefix}}-{{orderInfo.OfflineUser.PhoneNumber}}</td>
                </tr>
                <tr>
                    <td>邮箱</td>
                    <td>{{orderInfo.OfflineUser.Email}}</td>
                </tr>
            </table>
        </el-card>
        <!-- 商品信息（待确认支付、已支付定金可编辑） -->
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>商品信息</span>
            </div>
            <div v-if="this.status == 'UNPAID' || this.status == 'PRESELL_PAID'">
                <table class="order_detail host_detail">
                    <tr>
                        <td>商品</td>
                        <td>
                            <el-select v-model="productName" placeholder="请选择" style="width:100%">
                                <el-option
                                        v-for="(item, index) in productList"
                                        :label="item.Name+'（剩余库存：'+item.DefaultAttr.InStock+'）'"
                                        :value="item.ID"
                                        :key="index">
                                </el-option>
                            </el-select>
                        </td>
                    </tr>
                    <tr>
                        <td>类型</td>
                        <td>
                            <el-input v-model="productSellType" placeholder="请输入内容"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <td>单价</td>
                        <td>
                            <el-input v-model="productOriginPrice" placeholder="请输入内容"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <td>数量</td>
                        <td>
                            <el-input v-model="productAmount" placeholder="请输入内容"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <!-- 本条可改 -->
                        <td>交付日期</td>
                        <td>
                            <el-date-picker
                                    v-model="deliveryTime"
                                    type="datetimerange"
                                    start-placeholder="开始日期"
                                    end-placeholder="结束日期">
                            </el-date-picker>
                        </td>
                    </tr>
                </table>
                <el-button type="primary" style="margin:20px 0 0 29px" @click="originPriceSave">保存</el-button>
            </div>
            <div v-else>
                <table class="order_detail host_detail">
                    <tr>
                        <td>商品</td>
                        <td>{{orderInfo.Product.Name}}</td>
                    </tr>
                    <tr>
                        <td>类型</td>
                        <td>{{orderInfo.Product.SellType}}</td>
                    </tr>
                    <tr>
                        <td>单价</td>
                        <td>{{orderInfo.ProductOriginPrice}}</td>
                    </tr>
                    <tr>
                        <td>数量</td>
                        <td>{{orderInfo.Amount}}</td>
                    </tr>
                    <tr>
                        <td>交付日期</td>
                        <td>{{orderInfo.DeliveryStartTime | date}}----{{orderInfo.DeliveryEndTime | date}}</td>
                    </tr>
                </table>
            </div>
        </el-card>
        <!-- 收款信息（待确认支付可编辑） -->
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
