                <span>收款信息</span>
            </div>
            <div style="margin-bottom:20px">收款方式：{{payTitle}}</div>
            <div v-if="orderInfo.Payment == 'UnionPay' || orderInfo.PresellPayment == 'UnionPay'">
                <table class="order_detail host_detail">
                    <tr>
                        <td>收款人</td>
