                        <template slot-scope="scope">
                            <el-input v-model="scope.row.Amount" placeholder="" disabled></el-input>
                        </template>
                    </el-table-column>
                </el-table>
                <el-button type="primary" style="margin:20px 0 0 29px" @click="payInfoSave">保存</el-button>
            </div>
            <div v-else-if="orderInfo.Payment == 'USD' || orderInfo.PresellPayment == 'USD'">
                <table class="currencyTable">
                    <tr>
                        <td>Beneficiary Bank Name</td>
                        <td>DAH SING BANK LIMITED, HONG KONG</td>
                    </tr>
                    <tr>
                        <td>Bank code</td>
                        <td>040</td>
                    </tr>
                    <tr>
                        <td>Bank address</td>
                        <td>Hong Kong Branch</td>
                    </tr>
                    <tr>
                        <td>Swift Code</td>
                        <td>DSBAHKHH</td>
                    </tr>
                    <tr>
                        <td>Beneficiary Name</td>
                        <td>HALLEY PARTNERS LTD</td>
                    </tr>
                    <tr>
                        <td>Beneficiary Account Number</td>
                        <td>7470237285</td>
                    </tr>
                    <tr>
                        <td>Beneficiary Address</td>
                        <td>Hong Kong Branch</td>
                    </tr>
                </table>
                <el-table
                        :data="orderPayInfoList"
                        style="width: 100%;margin-top:20px"
                        border>
                    <el-table-column
                            prop="BeneficiaryName"
                            label="BeneficiaryName">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.BeneficiaryName" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="BankName"
                            label="BankName">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.BankName" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="BankCode"
                            label="BankCode">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.BankCode" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="Amount"
                            label="Amount">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.Amount" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                </el-table>
                <el-button type="primary" style="margin:20px 0 0 29px" @click="payInfoSave">保存</el-button>
            </div>
            <div v-else-if="orderInfo.Payment == 'BTC' || orderInfo.PresellPayment == 'BTC'">
                <table class="currencyTable">
                    <tr>
                        <td>BTC</td>
                        <td>1CppmSU6V2PWU6z6FThZyJsEZv4jt81bX8</td>
                    </tr>
                </table>
                <el-table
                        :data="orderPayInfoList"
                        style="width: 100%;margin-top:20px"
                        border>
                    <el-table-column
                            prop="BtcAddress"
                            label="地址">
                        <template slot-scope="scope">
                            <el-input v-model="scope.row.BtcAddress" placeholder=""></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column
