                    <table class="order_detail host_detail">
                        <tr>
                            <td>姓名</td>
                            <td>
                                <el-input
                                        v-model="name">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>电话</td>
                            <td>
                                <el-select v-model="prefix" placeholder="请选择" style="width:150px">
                                    <el-option v-for="(item, index) in countryList" :label="item.code" :value="item.id" :key="index"></el-option>
                                </el-select>
                                <el-input style="width:350px"
                                          v-model="phoneNumber">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>币种</td>
                            <td>
                                <el-select v-model="cryptocurrency" placeholder="请选择币种" style="width:150px">
                                    <el-option label="ETH" value="ETH"></el-option>
                                    <el-option label="ETC" value="ETC"></el-option>
                                    <el-option label="ZEC" value="ZEC"></el-option>
                                </el-select>
                            </td>
                        </tr>
                        <tr>
                            <td>钱包地址</td>
                            <td>
                                <el-input
                                        v-model="walletAddress">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>矿池</td>
                            <td>
                                <el-input
                                        v-model="miningPool">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>上线时间</td>
                            <td>
                                <el-date-picker
                                        v-model="startTime"
                                        type="date"
                                        placeholder="">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td>邮箱</td>
                            <td>
                                <el-input
                                        v-model="email">
                                </el-input>
                            </td>
                        </tr>
                    </table>
                </div>
                <div v-else>
                    <!-- OrderId=1011111458 -->
                    <table class="order_detail host_detail">
                        <tr>
                            <td>交付方式</td>
                            <td>发货</td>
                        </tr>
                        <tr>
                            <td>地区</td>
                            <td>{{countryArea}}</td>
                        </tr>
                        <tr>
                            <td>收件人</td>
                            <td>
                                <el-input
                                        v-model="name">
                                </el-input> 
                            </td>
                        </tr>
                        <tr v-if="countryArea == '国内'">
                            <td>手机</td>
                            <td>
                                <el-select v-model="prefixMobilePhone" placeholder="请选择" style="width:150px">
                                    <el-option v-for="(item, index) in countryList" :label="item.code" :value="item.id" :key="index"></el-option>
                                </el-select>
                                <el-input style="width:350px"
                                          v-model="mobilePhone">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>电话</td>
                            <td>
                                <el-input style="width:150px"
                                          v-model="prefix">
                                </el-input>
                                <el-input style="width:350px"
                                          v-model="phoneNumber">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>地址</td>
                            <td style="padding:10px 0 10px 12px">
                                <div v-if="countryArea=='国内'">
                                    <el-select v-model="provinceId" placeholder="省/直辖市" style="width:150px" @change='changeProvince'>
                                        <el-option v-for="(item, index) in areaList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-select v-model="cityId" placeholder="城市" style="width:150px" @change='changeCity'>
                                        <el-option v-for="(item, index) in cityList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-select v-model="regionId" placeholder="区/县" style="width:150px" @change='changeRegion'>
                                        <el-option v-for="(item, index) in regionList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-select v-model="streetId" placeholder="乡镇/街道" style="width:150px" @change='changeStreet'>
                                        <el-option v-for="(item, index) in streetList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-input
                                            style="margin: 10px 0 0 0"
                                            v-model="inAddress">
                                    </el-input>
                                </div>
                                <div v-if="countryArea=='国外'">

                                    <el-input v-model="Address1" placeholder="Stree address, P.O.box,company name" style="width:517px"></el-input>
                                    <br>
                                    <el-input v-model="Address2" placeholder="Apartment, suite, unit,building,floor" style="width:517px"></el-input>
                                    <br>
                                    <el-input v-model="City" placeholder="" style="width:517px"></el-input>
                                    <br>
                                    <el-input v-model="State" placeholder="State/Province/Region" style="width:517px"></el-input>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td>邮编</td>
                            <td>
                                <el-input
                                        v-model="zip">
                                </el-input>
                            </td>
                        </tr>
