                        <tr>
                            <td>姓名</td>
                            <td>{{hostingOrder.Name}}</td>
                        </tr>
                        <tr>
                            <td>手机</td>
                            <td>{{hostingOrder.Prefix}}-{{hostingOrder.MobilePhone}}</td>
                        </tr>
                        <tr>
                            <td>币种</td>
                            <td>{{hostingOrder.Cryptocurrency}}</td>
                        </tr>
                        <tr>
                            <td>钱包地址</td>
                            <td><a class="BTC_address" href="https://btc.com/" target="_Blank" style="color:#409EFF;">{{hostingOrder.Address}}</a></td>
                        </tr>
                        <tr>
                            <td>矿池</td>
                            <td>{{hostingOrder.MiningPool}}</td>
                        </tr>
                        <tr>
                            <td>上线时间</td>
                            <td>{{hostingOrder.StartTime | date}}</td>
                        </tr>
