                            <td>交付方式</td>
                            <td>发货</td>
                        </tr>
                        <tr>
                            <td>地区</td>
                            <td>国内</td>
                        </tr>
                        <tr>
                            <td>收件人</td>
                            <td>{{address.Receiver}}</td>
                        </tr>
                        <tr>
                            <td>手机</td>
                            <td>{{address.Mobilephone}}</td>
                        </tr>
                        <tr>
                            <td>电话</td>
                            <td>{{address.PhonePrefix}}-{{address.PhoneNumber}}</td>
