                                    </el-select>
                                    <el-select v-model="cityId" placeholder="城市" style="width:150px" disabled>
                                        <el-option v-for="(item, index) in cityList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-select v-model="regionId" placeholder="区/县" style="width:150px" disabled>
                                        <el-option v-for="(item, index) in regionList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
                                    <el-select v-model="streetId" placeholder="乡镇/街道" style="width:150px" disabled>
                                        <el-option v-for="(item, index) in streetList" :label="item.AreaName" :value="item.Id" :key="index"></el-option>
                                    </el-select>
