<script>
    import {sellType} from '@/fetch/const'
    export default {
        name: '',
        data(){
            return{
                sellType:sellType,
                payTitle:'',
                userName:'',
                bankName:'',
                cardNo:'',
                isHostingOrder:true,
                lang: 'cn',
                isDefault:false,
                status:'',
                remarks: '',
                OrderId:0,
                orderInfo: '',
                hostingOrder: '',
                address: '',
                uid: '',
                productList:[],
                productName: '',
                name: '',
                prefix: '',
                prefixMobilePhone:48,
                mobilePhone:'',
                cryptocurrency:'',
                walletAddress:'',
                miningPool:'',
                email:'',
                startTime:'',
                countryArea:'国内',
                countryList:[],
                phoneNumber:'',
                provinceId:'',
                areaList:[],
                cityId: '',
                cityList: [],
                regionId: '',
                regionList:[],
                streetId:'',
                streetList:[],
                inAddress:'',
                zip:'',
                addrId:'',
                countryId:'',
                orderPayInfoList:[],
                Address1:'',
                Address2:'',
                State:'',
                City:'',
                totalPrice:'',
                deliveryTime:[]
            }
        },
        methods: {
            getOderDetail(){
                this.$api('/admin/offline/get_offline_order_info', {
                    orderId:this.OrderId
                }).then(res => {
                    let d = res.data
                    // console.log(d)
                this.phoneNumber = ''
                this.totalPrice = d.totalPrice
                this.payTitle = '人民币'
                this.countryArea = '国内'
                this.status = d.order.Status
                this.orderInfo = d.order
                this.deliveryTime.push(this.orderInfo.DeliveryStartTime*1000)
                this.deliveryTime.push(this.orderInfo.DeliveryEndTime*1000)
                // console.log(this.deliveryTime)
                this.remarks = this.orderInfo.Remarks
                this.hostingOrder = d.hostingOrder
                this.address = d.address
                this.productName=this.orderInfo.Product.ID
                // SellType 1可发货，2可托管，3可发货可托管
                this.productSellType=this.orderInfo.Product.SellType
                this.productOriginPrice=this.orderInfo.ProductOriginPrice
                this.productAmount=this.orderInfo.Amount
                this.uid = d.order.OfflineUser.Id
                this.isHostingOrder = d.order.IsHostingOrder
                this.lang = d.order.Lang
                if(d.hostingOrder){
                    this.productId = d.order.Product.ID
                    this.name = d.hostingOrder.Name
                    this.addrId = d.hostingOrder.Id
                    this.prefix = d.hostingOrder.Prefix
                    this.phoneNumber = d.hostingOrder.MobilePhone
                    this.cryptocurrency = d.hostingOrder.Cryptocurrency
                    this.walletAddress = d.hostingOrder.Address
                    this.miningPool = d.hostingOrder.MiningPool
                    this.email = d.hostingOrder.Email
                    this.startTime = d.hostingOrder.StartTime*1000
                }else if(d.address){
                    this.countryId = d.address.CountryId
                    if(this.countryId != 48 && this.countryId != 1){
                        this.countryArea = '国外'
                    }
                    this.Address1 = d.address.Address1
                    this.Address2 = d.address.Address2
                    this.City = d.address.City
                    this.State = d.address.State
                    this.name = d.address.Receiver
                    this.prefix = d.address.PhonePrefix
                    this.mobilePhone = d.address.Mobilephone
                    this.phoneNumber = d.address.PhoneNumber
                    this.provinceId = d.address.ProvinceId
                    this.cityId = d.address.CityId
                    this.regionId = d.address.RegionId
                    this.inAddress = d.address.Address
                    this.streetId = d.address.StreetId
                    this.zip = d.address.Zip
                    this.addrId = d.orderAddress.ID
                    this.isDefault = d.address.IsDefault
                    if(this.orderInfo.Payment == 'UnionPay'){
                        this.payTitle = '人民币'
                    }else if(this.orderInfo.Payment == 'USD'){
                        this.payTitle = '美元'
                    }else if(this.orderInfo.Payment == 'BTC'){
                        this.payTitle = 'BTC'
                    }
                    if(this.countryId == 48){
                        this.getAllArea()
                        this.getCity()
                        this.getRegion()
                        this.getStreet()
                    }
                }
                if(d.orderPayInfoList){
                    this.orderPayInfoList = d.orderPayInfoList
                }else if(d.orderUsdHistory){
                    this.orderPayInfoList = d.orderUsdHistory
                }else if(d.orderBtcHistory){
                    this.orderPayInfoList = d.orderBtcHistory
                }

                // this.userName = d.orderPayInfoList[0].UserName
                // this.bankName = d.orderPayInfoList[0].BankName
                // this.cardNo = d.orderPayInfoList[0].CardNo
                // console.log(this.orderPayInfoList)
            });
            },
            remarksSave(){
                console.log(this.remarks)
                this.$api('/admin/offline/update_offline_order_remarks', {
                    orderId:this.OrderId,
                    remarks:this.remarks,
                    notice:'保存成功'
                }).then(res => {
                    console.log(res)
            });
            },
            originPriceSave(){
                // console.log(this.OrderId)
                this.$api('/admin/offline/update_offline_order_info', {
                    uid:this.uid,
                    orderId:this.OrderId,
                    productId: this.productName,
                    productAttrId: this.orderInfo.Attr.Id,
                    currencyType:this.productSellType,
                    unitPrice:this.productOriginPrice,
                    amount:this.productAmount,
                    deliverStartTime:  Date.parse(new Date(this.deliveryTime[0]))/1000,
                    deliverEndTime: Date.parse(new Date(this.deliveryTime[1]))/1000,
                    status: this.orderInfo.Status,
                    notice:'保存成功'
                }).then(res => {
                    console.log(res)
            });
            },
            getProductList(){
                this.$api('/admin/product/list', {
                    status: -1,
                    pageNo: 0,
                    pageSize: 10000
                }).then(res => {
                    // console.log(res)
                let d = res.data
                this.productList = d.list
            });
            },
            payInfoSave(){
                console.log(this.userName)
                console.log(this.orderPayInfoList)
                this.PaymentWay = 'CNY'
                if(this.orderInfo.Payment){
                    this.PaymentWay = this.orderInfo.Payment
                    if(this.orderInfo.Payment == 'UnionPay'){
                        this.PaymentWay = 'CNY'
                    }
                }else{
                    this.PaymentWay = this.orderInfo.PresellPayment
                    if(this.orderInfo.PresellPayment == 'UnionPay'){
                        this.PaymentWay = 'CNY'
                    }
                }
                this.orderPayInfoList.forEach((item, index) => {
                    this.$api('/admin/offline/update_cry_info', {
                    orderId: this.OrderId,
                    payId: item.Id,
                    receiver: item.UserName,
                    // amount: item.Amount,
                    remarks:item.Remarks,
                    receiptCurrency: this.PaymentWay,
                    bfyName:item.UserName?item.UserName:item.BeneficiaryName,
                    bfyBankName:item.BankName,
                    bankCode:item.CardNo?item.CardNo:item.BankCode,
                    banckAddress:item.BankAddress,
                    swiftCode:item.SwiftCode,
                    bfyAccountNumber:item.BeneficiaryAccount,
                    bfyAddress:item.BeneficiaryAddress,
                    btcAddress:item.BtcAddress,
                    needPay:item.Amount?item.Amount:item.NeedPay,
                    notice:'保存成功'
                }).then(res => {
                    console.log(res)
                let d = res.data
            });
            })
            },
            hostDetailSave(){
                console.log(this.isDefault)
                this.$api('/admin/offline/update_offline_order_address', {
                    orderId: this.OrderId,
                    addrId: this.addrId,
                    isHostOrder: this.isHostingOrder,
                    lang: this.lang,
                    receiver:this.name,
                    phonePrefix: this.prefix,
                    phoneNumber: this.phoneNumber,
                    zip: this.zip,
                    countryId: this.countryId,
                    isDefault:this.isDefault,
                    mobilephone: this.mobilePhone,
                    provinceId: this.provinceId,
                    cityId: this.cityId,
                    regionId: this.regionId,
                    streetId:this.streetId,
                    address: this.inAddress,
                    address1:this.Address1,
                    address2:this.Address2,
                    city:this.City,
                    state:this.State,
                    email: this.email,
                    cryptocurrency:this.cryptocurrency,
                    walletAddress:this.walletAddress,
                    miningPool:this.miningPool,
                    startTime: this.startTime/1000,
                    notice:'保存成功'
                }).then(res => {
                    console.log(res)

            });
            },
            changeProvince(){
                // console.log(this.provinceId)
                this.cityId = ''
                this.regionId = ''
                this.streetId = ''
                this.regionList = []
                this. streetList = []
                this.getCity()
            },
            changeCity(){
                // console.log(this.cityId)
                this.regionId = ''
                this.streetId = ''
                this. streetList = []
                this.getRegion()
            },
            changeRegion(){
                // console.log(this.regionId)
                this.streetId = ''
                this.getStreet()
            },
            changeStreet(){
                // console.log(this.streetId)
            },
            getAllCountry(){
                this.$api('/admin/default/get_all_country', {
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.countryList = res.data
            });
            },
            getAllArea(){
                this.$api('/admin/default/get_all_area', {
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.areaList = res.data
            });
            },
            getCity(){
                this.$api('/admin/default/get_city', {
                    provinceId: this.provinceId
                }).then(res => {
                    // console.log(res)
                let d = res.data
                this.cityList = res.data
            });
            },
            getRegion(){
                this.$api('/admin/default/get_region', {
                    cityId: this.cityId
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.regionList = res.data
            });
            },
            getStreet(){
                this.$api('/admin/default/get_street', {
                    regionId: this.regionId
                }).then(res => {
                    // console.log(res)
                    let d = res.data
                    this.streetList = res.data
            });
            }
        },
        created(){
            //此OrderId为order表的Id,返回信息中的Id
            this.OrderId = this.$route.query.OrderId
            // console.log(this.OrderId)
            this.getOderDetail()
            this.getProductList()
            this.getAllCountry()
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .order_detail{
        width: 100%;
        border-radius: 3px
    }
    .order_detail td{
        text-align: left;
        padding-left: 12px;
        height: 50px;
        border: 1px solid #ebeef5
    }
    .order_detail th{
        text-align: left;
        padding-left: 12px;
        color: #909399
    }
    .order_detail th{
        height: 35px;
        border: 1px solid #ebeef5
    }
    .remark_detail tr td:first-child, .reserve_detail tr td:first-child, .host_detail tr td:first-child{
        width: 15%
    }
    .currencyTable{
        margin-left: 95px;
        margin-bottom: 20px;
        font-size: 17px
    }
    .currencyTable tr td{
        border: 1px solid #999999
    }
    .currencyTable tr td:first-child{
        font-weight: 600;
        width: 150px;
        height: 30px;
        padding-left: 15px
    }
    .currencyTable tr td:nth-child(2){
        width: 400px;
        padding-left: 15px
    }
</style>


